.playericonscontainer{
    margin-left: 10px;
    position: relative;
}
.playerbutton{
    border-radius: 15px;
    position: relative; /*neu*/
    float: left;
    width: 45px;
    height: 45px;
}
.playericon{
    width: 100%;
    height: 100%;
}
.playericonoverlay{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
}
.playericon:hover {
    cursor: pointer;
    background-color: lightgrey;
}
.playericonoverlay:hover {
    cursor:pointer;
    opacity: 50%;
}
.nopebutton{
    border-radius: 25px;
    position: relative; /*neu*/
    float: left;
    /*margin: 10px;*/
    width: 150px;
    height: 130px;
    margin: 10px;
}
.nopeimage{/*neu*/
    display: block;
    width: 100%;
    height: 100%;
}
.nopeoverlay{/*neu*/
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    display: block;
    width: 100%;
    height: 100%;
}
.nopebutton:hover {
    cursor:-webkit-grab;
    cursor:grab;
    /*background-color: lightgrey;*/
}
.nopeoverlay:hover {
    cursor:-webkit-grab;
    cursor:grab;
    opacity: 20%;
}
.commentbutton{
    background-color: cadetblue;
    border-radius: 10px;
}
.commentbutton:hover {
    cursor: pointer;
    color: lightgrey;
    border-color: lightgrey;
